import React from "react"
import { Link, useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import indexStyles from "./index.module.css"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"

const Prologue = () => {
  const data = useStaticQuery(
    graphql`
      query{
        file(relativePath: { eq: "images/covers/Artistic_Mountain_Range.jpg" }) {
          childImageSharp {
            fluid(maxWidth: 960, maxHeight: 450) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `
  )

  return (
    <section role="main">
      <Row className={`${indexStyles.imageBox} no-margin`}>
        <div className={`${indexStyles.imageFilter} d-none d-sm-block`}></div> 
        <Img fluid={data.file.childImageSharp.fluid} alt="cover"/>
        <h1 className={`${indexStyles.overlayTitle} d-none d-sm-block`}>{"Ready for change?"}</h1>
      </Row>
      <Row className={`${indexStyles.presentation} justify`}>
        <Col xs={12} md={11} lg={11} xl={11}>
          <h1 className={"m-2em"}>Mission</h1>
          <p>
            We wish to support the growth of charities and social enterprises.
            Our greatest asset is our <Link to="/about/">team</Link>, a young group of friends with a diverse background in engineering and physical sciences.
            Our unique combination of knowledge and skills will help you identify the potential customers, suppliers and business partners according to your needs.
          </p>
          <h1 className={"m-2em"}>Discover</h1>
          <p>
            Our journey has lead us to learn a lot.
            Along the way, we have had the opportunity to meet many people that have shared their experiences with us.
            Read more in our <Link to="/stories/">stories</Link> page.
          </p>
        </Col>
      </Row>
    </section>
  )
}

export default Prologue
